import { useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import styled from "styled-components";

function BookReportPage() {
  const bookReportList = useSelector((state) => {
    const set = new Set(state.bookReportList);
    const uniqueArr = [...set];
    return uniqueArr;
  });

  console.log(bookReportList);

  const { id } = useParams();
  const findProduct = bookReportList.find(function (obj) {
    return obj.isbn === id;
  });
  console.log(findProduct);

  return (
    <>
      {bookReportList.length === 0 ? null : (
        <div>
          <figure
            style={{
              display: "inline-block",
              border: "1px solid black",
              padding: "10px",
            }}
          >
            <img src={findProduct.thumbnail} alt="" />
          </figure>
          <List>
            <li>
              <Question>
                Q.    ?   .
              </Question>
              <form>
                <TextArea></TextArea>
              </form>
            </li>
            <li>
              <Question>Q.     ?</Question>
              <Form>
                <TextArea></TextArea>
              </Form>
            </li>
            <li>
              <Question>Q.       ?</Question>
              <form>
                <TextArea></TextArea>
              </form>
            </li>
          </List>
        </div>
      )}
    </>
  );
}

export default BookReportPage;

const List = styled.ul`
  margin: 120px 0 0 0;

  li {
    margin: 0 0 80px 0;
  }
`;

const Question = styled.p`
  font-size: 20px;
  margin: 0 0 20px 0;
`;

const Form = styled.form`
  font-size: 0;
`;
const TextArea = styled.textarea`
  width: 100%;
  height: 200px;
  padding: 20px;
  border: 1px solid #000;
  box-sizing: border-box;
  outline: none;
  resize: none;
`;
