// 
import { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";

//  
import styled from "styled-components";

function Header() {
  const [menuClick, setMenuClick] = useState(false);
  const clickRef = useRef();

  useEffect(() => {
    const handleOutsideClose = (e) => {
      // useRef current        
      if (menuClick && !clickRef.current.contains(e.target))
        setMenuClick(false);
    };
    document.addEventListener("click", handleOutsideClose);

    return () => document.removeEventListener("click", handleOutsideClose);
  }, [menuClick]);

  return (
    <SectionHeader>
      <h1 className="blind">  </h1>
      <Inner>
        <Link
          to="/"
          onClick={() => {
            setMenuClick(false);
          }}
        ></Link>
        {/* <Gnb>
          <List className="list">
            <Item>
              <Link to="/best"></Link>
            </Item>
            <Item>
              <Link to="/event"></Link>
            </Item>
            <Item>
              <Link to="/ShoppingBasket"></Link>
            </Item>
          </List>
        </Gnb> */}
        <Hamburger>
          <MenuButton
            ref={clickRef}
            onClick={() => {
              if (menuClick) {
                setMenuClick(false);
              } else {
                setMenuClick(true);
              }
            }}
          >
             
          </MenuButton>
          {menuClick === true ? (
            <HamburgerMenu className="on">
              <div>
                <ul>
                  <li>
                    <Link
                      to="/event"
                      onClick={() => {
                        setMenuClick(false);
                      }}
                    >
                      
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="/ShoppingBasket"
                      onClick={() => {
                        setMenuClick(false);
                      }}
                    >
                      
                    </Link>
                  </li>
                  <li>
                    <Link
                      to="/BookReport"
                      onClick={() => {
                        setMenuClick(false);
                      }}
                    >
                      
                    </Link>
                  </li>
                </ul>
              </div>
            </HamburgerMenu>
          ) : null}
        </Hamburger>
      </Inner>
    </SectionHeader>
  );
}

export default Header;

let SectionHeader = styled.header``;

let Inner = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  padding: 22px 0;

  > a {
    font-size: 18px;
    background-image: url("/books_logo.png");
    background-repeat: no-repeat;
    background-size: auto 44px;
    background-position: center;
    width: 37px;
    height: 44px;
  }
`;

