//   margin: 0 24px 0 0;

//   > a {
//     font-size: 14px;
//   }

//   :last-child {
//     margin: 0;
//   }
// `;

//   
let Hamburger = styled.div`
  position: relative;
`;

let MenuButton = styled.button`
  padding: 10px;
  border: 1px solid #000;
`;

let HamburgerMenu = styled.div`
  position: absolute;
  top: calc(100% - 1px);
  right: 0;
  left: 0;
  z-index: 1000;
  border: 1px solid #000;
  background-color: #fff;

  div {
  }

  ul {
  }

  li {
    color: #fff;

    &:last-child {
      margin: 0;
    }
  }

  a {
    padding: 4px 0;
    text-align: center;

    &:hover {
      color: #fff;
      background-color: #000;
    }
  }
`;
